.description-enter {
    opacity: 0.01;
    height: 0;
}
.description-enter-active {
    opacity: 1;
    height: 200px;
    transition: opacity 400ms, height 800ms ease-out;
}
.description-enter-done {
    height: 200px;
}
.description-exit {
    opacity: 1;
    height: 200px;
}
.description-exit-active {
    opacity: 0.01;
    height: 0;
    transition: opacity 400ms, height 800ms ease-out;
}

#skills #skills-container .skills .skill-item-enter {
    opacity: 0.01;
    width: 0%;
}
#skills #skills-container .skills .skill-item-enter-active {
    opacity: 1;
    width: 15%;
    transition: all 500ms;
}
#skills #skills-container .skills .skill-item-enter-done {
    width: 15%;
}
#skills #skills-container .skills .skill-item-exit {
    opacity: 1;
    width: 15%;
}
#skills #skills-container .skills .skill-item-exit-active {
    opacity: 0.01;
    width: 0%;
    transition: all 500ms;
}

@media screen and (max-width: 576px) {
    #skills #skills-container .skills .skill-item-enter {
        opacity: 0.01;
        width: 0%;
    }
    #skills #skills-container .skills .skill-item-enter-active {
        opacity: 1;
        width: 65%;
        transition: all 500ms;
    }
    #skills #skills-container .skills .skill-item-enter-done {
        width: 65%;
    }
    #skills #skills-container .skills .skill-item-exit {
        opacity: 1;
        width: 65%;
    }
    #skills #skills-container .skills .skill-item-exit-active {
        opacity: 0.01;
        width: 0%;
        transition: all 500ms;
    }
}